@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.tabs-list{display:flex;padding:0;grid-gap:15px;gap:15px;list-style:none;border-bottom:1px solid #aaa3a3;margin:0;width:640px}.button{height:30px;width:70px;padding:0;outline:none;border:none;background-color:#d1d4a1;color:#fff;border-top-left-radius:10px;border-top-right-radius:10px;cursor:pointer}.button--active{background-color:#5d86ce;color:black;font-weight:bold}.show-tabs{background-color:rgba(214,192,192,0.57);height:350px;width:598px;border:1px solid #cec6c6;border-bottom-left-radius:15px;border-bottom-right-radius:15px;padding:10px 20px}

body{margin:0;display:flex;justify-content:center;background-color:#fff;background-image:linear-gradient(160deg, #70b1d6 0%, #80d0c7 100%);height:100vh;font-family:"Poppins", sans-serif}h1{margin:0}.App{margin-top:20px}.app__article{margin-bottom:50px;text-align:center}

